export default function (t = 0) {
    let msec
    Number.parseInt(t % 1000)<100? msec = appendZero(Number.parseInt(t % 1000)): msec = appendZero(Number.parseInt(t % 1000/10))
    
    // const msec = appendZero(Number.parseInt(t % 1000))
    const sec = appendZero(Number.parseInt(t/1000%60))
    const min = appendZero(Number.parseInt(t/60000%60));

    return `${min}:${sec}.${msec}`;

}

const appendZero = n=>n.toLocaleString({},{minimumIntegerDigits:2});