
import formateTime from '@/tool/formateTime';
import React from 'react';

export default class TimeDisplay extends React.Component {

    shouldComponentUpdate(nextProps, nextState) {
        if(this.props.nowVideoTime!=nextProps.nowVideoTime)
            {return true;}
        else
        {
            if(this.props.allVideoTime!=nextProps.allVideoTime)
                return true;
            else
                return false;
        }
    }

    render(){
        return(
            <div className='time-display'>
                <div className="time-wrapper">
                    <div className='now-time'>{formateTime(this.props.nowVideoTime)}</div>
                    /
                    <div className='all-time'>{formateTime(this.props.allVideoTime)}</div>
                </div>
            </div>
        )
    }
}